import { useForm, Controller } from "react-hook-form";
import styled from "styled-components";
import axios from "axios"; //     axios 

const distances = ["", "", ""];
const infra = [
  { key: "subwayStation", name: "" },
  { key: "publicParking", name: "" },
  { key: "traditionalMarket", name: "" },
  { key: "policeStation", name: "" },
  { key: "kindergarten", name: "" },
  { key: "elementarySchool", name: "" },
  { key: "middleSchool", name: "" },
  { key: "highSchool", name: "" },
  { key: "beach", name: "" },
  { key: "park", name: "" },
];
const facilities = [
  { key: "studyRoom", name: "" },
  { key: "tutorAcademy", name: "" },
  { key: "artAcademy", name: "" },
  { key: "foreignLanguageAcademy", name: "" },
  { key: "musicAcademy", name: "" },
  { key: "danceAcademy", name: "" },
  { key: "slicedRawFish", name: "" },
  { key: "koreanFood", name: "" },
  { key: "japaneseFood", name: "" },
  { key: "chineseFood", name: "" },
  { key: "coffeeShop", name: "" },
  { key: "convenienceStore", name: "" },
  { key: "crimePreventionFacility", name: "" },
];

interface FormData {
  distances: {
    [key: string]: string;
  };
  facilities: string[];
}

export const Survey = () => {
  const [isSettlementResult, setIsSettlementResult] = useRecoilState(
    settlementSurveyResultState
  );

  const { handleSubmit, control, setValue, watch } = useForm<FormData>({
    defaultValues: {
      distances: {},
      facilities: [],
    },
  });

  const watchFacilities = watch("facilities");

  //    
  const onSubmit = async (data: any) => {
    const requestData = {
      //      
      subwayStation: data.distances[""],
      publicParking: data.distances[""],
      traditionalMarket: data.distances[""],
      policeStation: data.distances[""],
      kindergarten: data.distances[""],
      elementarySchool: data.distances[""],
      middleSchool: data.distances[""],
      highSchool: data.distances[""],
      beach: data.distances[""],
      park: data.distances[""],
      //    boolean  
      studyRoom: watchFacilities.includes(""),
      tutorAcademy: watchFacilities.includes(""),
      artAcademy: watchFacilities.includes(""),
      foreignLanguageAcademy: watchFacilities.includes(""),
      musicAcademy: watchFacilities.includes(""),
      danceAcademy: watchFacilities.includes(""),
      slicedRawFish: watchFacilities.includes(""),
      koreanFood: watchFacilities.includes(""),
      japaneseFood: watchFacilities.includes(""),
      chineseFood: watchFacilities.includes(""),
      coffeeShop: watchFacilities.includes(""),
      convenienceStore: watchFacilities.includes(""),
      crimePreventionFacility: watchFacilities.includes(""),
    };

    console.log(requestData);

    //  POST  
    // try {
    //   const response = await axios.post("/api/settlement", requestData);
    //   console.log("Server Response:", response.data);
    //   setIsSettlementResult(true);
    // } catch (error) {
    //   console.error("Error posting data:", error);
    // }
  };

  const handleAmenitySelect = (amenity: string) => {
    const selectedFacilities = watchFacilities.includes(amenity)
      ? watchFacilities.filter((a: string) => a !== amenity)
      : [...watchFacilities, amenity];
    setValue("facilities", selectedFacilities);
  };

  return (
    <Wrapper>
      <form onSubmit={handleSubmit(onSubmit)}>
        <div>
          <Question>
               <BoldText></BoldText> .
          </Question>
          <Table>
            <thead>
              <tr>
                <th></th>
                {distances.map((distance) => (
                  <th key={distance}>{distance}</th>
                ))}
              </tr>
            </thead>
            <tbody>
              {infra.map((facility) => (
                <tr key={facility.key}>
                  <td>{facility.name}</td>
                  {distances.map((distance) => (
                    <td key={`${facility.key}-${distance}`}>
                      <Controller
                        control={control}
                        name={`distances.${facility.name}`}
                        render={({ field: { onChange, value } }) => (
                          <Checkbox
                            type="checkbox"
                            checked={value === distance}
                            onChange={() => onChange(distance)}
                          />
                        )}
                      />
                    </td>
                  ))}
                </tr>
              ))}
            </tbody>
          </Table>
          <Question>
                <BoldText></BoldText> .
          </Question>
          <FacilitiesWrapper>
            {facilities.map((amenity) => (
              <AmenityButton
                key={amenity.key}
                $isSelected={watchFacilities.includes(amenity.name)}
                onClick={() => handleAmenitySelect(amenity.name)}
              >
                {amenity.name}
              </AmenityButton>
            ))}
          </FacilitiesWrapper>
        </div>
        <SubmitButton type="submit">
          <Icon size={16} name={ICONS.LOCATION_RECOMMAND} />{" "}
          <p>  </p>
        </SubmitButton>
      </form>
    </Wrapper>
  );
};

export default Survey;

