function mappingMarker(type: string) {
  if (type === "tourist_attraction")
    return MARKERS_PATHS.TOURIST_ATTRACTION_MARKER;
  else if (type === "shopping") return MARKERS_PATHS.SHOPPING_MARKER;
  else if (type === "experience") return MARKERS_PATHS.EXPERIENCE_MAEKER;
  else if (type === "accommodation") return MARKERS_PATHS.ACCOMMODATION_MARKER;
  else if (type === "restaurant") return MARKERS_PATHS.RESTAURANT_MARKER;
  else if (type === "location") return MARKERS_PATHS.LOCATION_MARKER;
  else return MARKERS_PATHS.STAR_MARKER;
}

const containerStyle = {
  width: "100vw",
  height: "100vh",
};

const center = {
  lat: 35.17013,
  lng: 129.12848,
};

function MyMapComponent() {
  const location = useLocation();
  const [course, setCourse] = useState<Course[]>([]);
  const GOOGLE_MAPS_API_KEY = "" + process.env.REACT_APP_GOOGLE_MAPS_API_KEY;
  const { isLoaded } = useJsApiLoader({
    googleMapsApiKey: GOOGLE_MAPS_API_KEY, //  API  
    libraries: ["places"],
  });
  const selectedRestaurantCoord = useRecoilValue(selectedRestaurantCoordState);
  const selectedFestivalCoord = useRecoilValue(selectedFestivalCoordState);
  const currentCoord = useRecoilValue(currentCoordState);

  useEffect(() => {
    setCourse(data);
  }, []);

  const [directions, setDirections] =
    useState<google.maps.DirectionsResult | null>(null);

  const calculateRoute = useCallback((origin: any, destination: any) => {
    const directionsService = new google.maps.DirectionsService();

    directionsService.route(
      {
        origin,
        destination,
        travelMode: google.maps.TravelMode.TRANSIT, //    (WALKING, BICYCLING, TRANSIT )
      },
      (result, status) => {
        if (status === "OK" && result) {
          setDirections(result);
        } else {
          console.error(`error fetching directions ${result}`);
        }
      }
    );
  }, []);

  return isLoaded ? (
    <div>
      <GoogleMap mapContainerStyle={containerStyle} center={center} zoom={15}>
        {/*        */}
        {location.pathname.includes(ROUTES.AROUND) && currentCoord && (
          <Marker
            position={{
              lat: currentCoord.lat,
              lng: currentCoord.lng,
            }}
            icon={mappingMarker("location")}
          />
        )}
        {/*              */}
        {location.pathname.includes(ROUTES.AROUND_RESTAURANT) &&
          selectedRestaurantCoord && (
            <Marker
              position={{
                lat: selectedRestaurantCoord.lat,
                lng: selectedRestaurantCoord.lng,
              }}
              icon={mappingMarker("restaurant")}
            />
          )}

        {/*    /   /    /   */}
        {location.pathname.includes(ROUTES.AROUND_FESTIVAL) &&
          selectedFestivalCoord && (
            <Marker
              position={{
                lat: selectedFestivalCoord.lat,
                lng: selectedFestivalCoord.lng,
              }}
              icon={mappingMarker("tourist_attraction")}
            />
          )}
        {location.pathname === ROUTES.TEAVEL && (
          <>
            {directions && (
              <>
                <DirectionsRenderer
                  directions={directions}
                  options={{ suppressMarkers: true }}
                />
              </>
            )}
            {/*     */}
            {course &&
              course.map((location, i) => (
                <Marker
                  key={i}
                  position={{ lat: location.latitude, lng: location.longitude }}
                  icon={mappingMarker(location.type)} //    
                />
              ))}
          </>
        )}
      </GoogleMap>
      {location.pathname === ROUTES.TEAVEL && (
        <ButtonWrapper>
          <button
            onClick={() =>
              calculateRoute(
                { lat: 35.07885, lng: 129.04402 },
                { lat: 35.092648, lng: 129.03255 }
              )
            }
          >
              1 (   )
          </button>

          <button
            onClick={() =>
              calculateRoute(
                { lat: 35.092648, lng: 129.03255 },
                { lat: 35.243847, lng: 129.21524 }
              )
            }
          >
              2 (   )
          </button>
          <button
            onClick={() =>
              calculateRoute(
                { lat: 35.243847, lng: 129.21524 },
                { lat: 35.17013, lng: 129.12848 }
              )
            }
          >
              3 (  )
          </button>
          <button
            onClick={() =>
              calculateRoute(
                { lat: 35.17013, lng: 129.12848 },
                { lat: 35.12819599, lng: 128.8539747 }
              )
            }
          >
              4 (  )
          </button>

          <button
            onClick={() =>
              calculateRoute(
                { lat: 35.12819599, lng: 128.8539747 },
                { lat: 35.177387, lng: 128.95245 }
              )
            }
          >
              5 (  )
          </button>
        </ButtonWrapper>
      )}
    </div>
  ) : (
    <div>  ...</div>
  );
}

